.tags-h1 {
    margin-top: 40px;
    font-weight: 400;
}

.tags-p {
    margin: 0px;
    font-size: 15px;
    /* margin-top: -9px; */
    /* background-color: #39739d;
    top: 10px; */
}

.tags-list-container {
    padding: 20px 0px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 10px;
}

.tag {
    padding: 10px;
    border: solid 1px #d2d2d2;
    border-radius: 2px;
}

.tag h4 {
    display: inline-block;
    margin: 10px 0px;
    padding: 5px 5px;
    background-color: #e1ecf4;
    color: #39739d;
}

.tag p {
    font-size: 14px;
    color: #323232;
    line-height: 17px;
}